.topnav {
  background-color: #cbcbcb;
  opacity: 0.95;
  overflow: hidden;
  position: fixed;
  width:100%;
  top:0;
  width:64%;
  z-index:1;
}
@media (max-width:1500px) {
  .topnav {
    width:calc(100% - 55px);
  }
}
@media (min-width:2000px) {
  .topnav {
    width:65%;
  }

}


.topnav a {
  float: left;
  display: block;
  color: #333;
  text-align: center;
  padding: 12px 14px;
  text-decoration: none;
  font-size: 18px;
  transition: 0.3s ease-in-out;
  font-family: 'Open Sans', sans-serif;
}

.topnav a:hover {
  color: #000000;
}

.topnav .icon {
  display: none;
}
 
 @media screen and (max-width: 600px) {
  .topnav a:not(:first-child) {display: none;}
  .topnav a.icon {
    float: right;
    display: block;
  }
}


@media screen and (max-width: 600px) {
  .topnav.responsive {position: relative;}
  .topnav.responsive a.icon {
    position: absolute;
    right: 0;
    top: 0;
  }
  .topnav.responsive a {
    float: none;
    display: block;
    text-align: left;
  }
} 
